<template>
  <div>
    <div class='demo'>
      <video-player class="video-player-box"
                    ref="videoPlayer"
                    :playsinline="true"
                    :options="playerOptions">
      </video-player>
    </div>
  </div>
</template>

<script>
import 'video.js/dist/video-js.css'
import { videoPlayer } from 'vue-video-player'

export default {
  name: "VideoDetail",
  components: {
    videoPlayer
  },
  data() {
    return {
      // id: this.$route.query.id,
      // video: {},
      playerOptions: {
        playbackRates: [0.5, 1.0, 1.5, 2.0], // 
        autoplay: true, // true,
        muted: false, // 
        loop: false, // 
        preload: 'auto', // <video>auto,
        language: 'zh-CN',
        aspectRatio: '16:9', //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [{
          type: "video/mp4", // 
          src: '' // url
        }],
        poster: '', // 
        notSupportedMessage: '', // Video.js
        controlBar: {
          timeDivider: true, // 
          durationDisplay: true, // 
          remainingTimeDisplay: true, // 
          fullscreenToggle: true // 
        }
      }
    }
  },
  created() {
    let id =  this.$route.query.id
    this.request("/file/detail/" + id).then(res => {
      console.log(res.data)
      // this.video = res.data
      this.playerOptions.sources[0].src = res.data.url
      this.playerOptions.sources[0].src = "http://vjs.zencdn.net/v/oceans.mp4"
    })
  },
  methods: {}
}
</script>

